.grey-number{
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
    margin-right: 20px;;
    /* identical to box height, or 36px */


    color: #DEDEDE;
}

.video-part{
    width: 100%;
    height: 600px;
    iframe{
        width: 100%;
        height: 100%
    }
}


@media screen and (max-width: 768px){
    .grey-number{
      font-size: 18px;
    }
    .video-part{
        height: 450px;
    }
  }